<template>
  <div id="myChart" ref="chart" style="width:600px;height:320px;background: #d1d1d1;position: absolute;top:0"  v-resize="redraw"></div>
</template>
<script>
export default {
  name: 'LindChart',
  data () {
    return {
      myChart: {},
      data: {},
      xAxisData: [],
      yAxisTablet: []
    }
  },
  props: {
    lineData: {}
  },
  watch: {
    lineData (val) {
      console.log(val)
      let arrx = []
      for (let item of val.data) {
        arrx.push(item.date)
      }
      this.xAxisData = arrx
      let arry = []
      for (let item of val.data) {
        arry.push(item.sum + val.beforeTotal)
      }
      this.yAxisTablet = arry
      console.log(this.xAxisData)
      console.log(this.yAxisTablet)
      this.drawLine()
    }
  },
  methods: {
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '系统日志统计',
          left: 35,
          top: 10
        },
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            return params[0].name + '：' + params[0].value + '个'
          }
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '7%',
          top: '80px',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: this.xAxisData
          }
        ],
        yAxis: {
          type: 'value',
          name: '数量/个',
          minInterval: 1
        },
        series: [
          {
            name: '日志数量',
            type: 'line',
            data: this.yAxisTablet
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    },
    getimg () {
      let img = new Image()
      img.src = this.myChart.getDataURL({
        type: 'png',
        pixelRatio: 2,
        backgroundColor: '#d1d1d1'
      })
      return img
    }
  }
}
</script>

